import "rc-slider/assets/index.css";
import "rc-tooltip/assets/bootstrap.css";

import React, { Component } from "react";
import PrismCode from "react-prism";
import TruncateString from "react-truncate-string";
import Slider from "rc-slider";
import Tooltip from "rc-tooltip";

import "./App.css";

const Handle = Slider.Handle;

const handle = props => {
  const { value, dragging, index, ...restProps } = props;
  return (
    <Tooltip
      prefixCls="rc-slider-tooltip"
      overlay={value}
      visible={dragging}
      placement="top"
      key={index}
    >
      <Handle value={value} {...restProps} />
    </Tooltip>
  );
};

const string1 =
  "It truncates long long long long long long long long long long long long long long long strings in the middle!";

class App extends Component {
  state = {
    truncate: 50
  };

  onChangePercentage = truncate => {
    this.setState({ truncate });
  };

  render() {
    const wrapperStyle = { width: "40%" };

    return (
      <div className="App">
        <div className="container containerA">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>
        <div className="container containerB">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>
        <div className="container containerC">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>
        <div className="container containerD">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>
        <div className="container containerE">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>
        <div className="container containerF">
          <TruncateString text={string1} truncateAt={this.state.truncate} />
        </div>

        <div style={wrapperStyle}>
          <h2>Usage:</h2>

          <div>
            <h2>
              <PrismCode component="pre" className="language-javascript">
                {`<TruncateString text={myString}${
                  this.state.truncate === 50
                    ? ``
                    : ` truncateat={${this.state.truncate}}`
                } />`}
              </PrismCode>
            </h2>
          </div>

          <Slider
            min={0}
            max={100}
            defaultValue={this.state.truncate}
            handle={handle}
            onChange={this.onChangePercentage}
          />
        </div>

        <div>
          <h1>
            <a href="https://codesandbox.io/s/xvv9r5ozo">codesandbox</a>
          </h1>
        </div>
      </div>
    );
  }
}

export default App;



